.modal-wrapper{
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.4);
}

.modal-form{
    border-radius: 5px;
    padding: 2rem;
    background-color: white;
    width: 25rem;
    height: 20rem;
}

.modal-form .btn {
    display: block;
    margin: auto;
}

.error {
    background-color: #fac1cb;
    color: rgb(202, 41, 41);
    border-radius: 5px;
    justify-content: space-around;
    width: 100%;
    padding: 5px;
    margin-bottom: 0.5rem;
}