.table-wrapper{
    width: 100%;
}

.table{
    display: block;
    overflow: hidden;
    table-layout: auto;
    border-collapse: collapse;
    box-shadow: 0px 10px 10px #ccc;
    border-radius: 10px;
    white-space: nowrap;
    width: 100rem;
    max-width: 80%;
    margin: auto;
    overflow-x: auto;

}

.table thead {
    background-color: #ccc;
    color: #222;
}

.table th, table td  {
    padding: 0.8rem;
}

.table tbody tr:hover {
    background-color: #eee;
}
.table tbody tr{
    background-color: white;
}

.table td {
    border-top: 0.5px solid #ddd;
    overflow: hidden;
    text-overflow: ellipsis;
}

.expand {
    width: 100%;
}

.label {
    border-radius: 3px;
    padding: 0.3rem;
    color: white;
}

.label-done {
    background-color: #42a942
}

.label-pending {
    background-color: #777
}

.label-missed {
    background-color: #d9534f
}

.actions {
    display: flex;
    justify-content: space-around;
}

.actions svg {
    cursor: pointer;
}

.delete-btn{
    color: #ca1b1b;
}

.edit-btn{
    color: #524f4f;
}